body {
  display: flex;
  justify-content: center;
  padding: 15px;
  background: #7f7fd5;
  background: -webkit-linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
  height: 100vh;
  overflow-y: auto;
  box-sizing: border-box;
}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.shopping-button {
  font-size: 1.5rem;
  margin: 0 0.1rem;
  background-color: transparent;
}
.list {
  width: 400px;
  background-color: rgba(0, 0, 0, 0.3);
  margin: auto;
  border-radius: 20px;
}
.header {
  height: 48px;
  padding: 8px;
  font-size: 24px;
  background-color: whitesmoke;
  border-radius: 20px 20px 0 0;
}

.items {
  height: 200px;
  background-color: darkgray;
}

.shopping {
  font-size: 1.5rem;
  padding: 0.5rem;
  list-style: none;
}

.shopping-increase,
.shopping-decrease {
  color: cornflowerblue;
}

.shopping-delete {
  font-size: 1.2rem;
  color: darkred;
}
